
.delete {
    margin-top: 196px;
}

.slim__block {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 280px;
    margin: 0 auto;

    border: 1px solid rgb(228, 92, 114);
}

.slim__info {
    padding: 39px 20px 22px 20px;
    background-color: $bg-grey;
    position: relative;
    z-index: -2;

    border: 1px solid rgb(92, 228, 149);

    &:first-child{
        margin-bottom: 20px;
    }
}

.slim__title {
    color: #111;
    @extend %oswald-400;
    font-size: 24px;
    line-height: 37.02px;
    text-align: left;
    text-transform: uppercase;

    position: relative;
    padding-left: 72px;

    &::before{
        content: "";
        display: block;
        width: 50px;
        height: 50px;
        background-color: $bg-color;
        border-radius: 50%;
        position: absolute;
        top: -4px;
        left: 0;
        z-index: -1;
    }
}


.slim__text {
    color: #444;
    font-size: 14px;
    line-height: 18px;

    padding-top: 36px;
    padding-bottom: 23px;
    margin-bottom: 20px;
    border-bottom: 1px solid #dadada;
    
}

.slim__text-catalog {
    color: #111;
    @extend %oswald-400;
    font-size: 16px;
    line-height: 16px;
    text-align: left;
    text-transform: uppercase;
}